#NavBar {
  margin: 10vh auto;
  max-width: 600px;
}

#NavBar .nameDisplay {
  font-size: 1.5em;
}

#leftColumn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#leftColumn .button {
  margin: 30px auto;
}

#rightColumn {
  font-size: 1.2em;
}

#rightColumn u {
  text-decoration: underline;
  font-weight: bold;
  color: teal;
}

#rightColumn a {
  text-decoration: underline;
  font-weight: bold;
  color: black;
}

.meta {
  margin-top: 20px;
}

.meta .stacks {
  padding: 1px 1px;
  color: white;
  background-color: rgba(0, 128, 128, 0.822);
}

.footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100vw;
  padding: 5px 5px 5px 5px;
  background-color: rgba(0, 128, 128, 0.781);
  color: white;
}

.footer p {
  text-align: center;
}

.footer a {
  color: white;
  text-decoration: underline;
}

#page-container {
  position: relative;
  min-height: 100vh;
}
#content-wrap {
  padding-bottom: 4rem;
  max-width: 700px;
  width: 90%;
  margin: 0 auto;
}

